import React, { useState } from "react";
import AdminNavigation from "../../components/AdminNavigation";
import Header from "../../components/Navigation/Header";
import styles from "../styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import { addAdminstrator } from "../../redux/actions/adminActions";
import { Center, CircularProgress, useToast } from "@chakra-ui/react";
import { ADMIN_ADD_RESET } from "../../redux/constants/adminConstants";

const Admin = () => {
  const [firstname, setFirstname] = useState("");
  const [lastname, setLastname] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  const dispatch = useDispatch();
  const toast = useToast();

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(addAdminstrator(firstname, lastname, email, password));
  };

  const addAdmin = useSelector((state) => state.addAdmin);
  const { loading, error, success } = addAdmin;

  if (error) {
    toast({
      title: "Error",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: ADMIN_ADD_RESET });
  }

  if (success) {
    toast({
      title: "Notification",
      description: "Admin Added successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: ADMIN_ADD_RESET });
  }

  return (
    <div>
      <AdminNavigation />
      <div className={styles.page}>
        <Header title="Admin" name="John Doe" />
        <div className={styles.contentPadding}>
          {loading ? (
            <Center>
              {" "}
              <CircularProgress isIndeterminate color="purple.500" />
            </Center>
          ) : (
            <form onSubmit={submitHandler}>
              <div className={styles.threeGrid}>
                <div className={styles.inputContainer}>
                  <label>Firstname</label>
                  <input
                    type="text"
                    onChange={(e) => setFirstname(e.target.value)}
                    vlaue={firstname}
                  />
                </div>
                <div className={styles.inputContainer}>
                  <label>Lastname</label>
                  <input
                    type="text"
                    onChange={(e) => setLastname(e.target.value)}
                    value={lastname}
                  />
                </div>
                <div className={styles.inputContainer}>
                  <label>Email</label>
                  <input
                    type="email"
                    onChange={(e) => setEmail(e.target.value)}
                    value={email}
                  />
                </div>
                <div className={styles.inputContainer}>
                  <label>Password</label>
                  <input
                    type="password"
                    onChange={(e) => setPassword(e.target.value)}
                    value={password}
                  />
                </div>
                <div className={styles.inputContainer}>
                  <label>Confirm Password</label>
                  <input
                    type="password"
                    onChange={(e) => setConfirmPassword(e.target.value)}
                    value={confirmPassword}
                  />
                </div>
                <div className={styles.inputContainer}>
                  <label style={{ visibility: "hidden" }}>Submit</label>
                  <button className={styles.purple} type="submit">
                    Add Admin
                  </button>
                </div>
              </div>
            </form>
          )}
        </div>
      </div>
    </div>
  );
};

export default Admin;
