import React from "react";
import styles from "./styles.module.css";
import { Link } from "react-router-dom";
import {
  AiFillAppstore,
  AiOutlineUser,
  AiOutlineLaptop,
  AiFillFund,
  AiOutlineLogout,
} from "react-icons/ai";
import logo from "../../assets/logo.jpg";

const Navigation = () => {
  return (
    <div className={styles.navigation}>
      <div className={styles.logo}>
        <img src={logo} alt="Logo" />
      </div>
      <div className={styles.url}>
        <ul>
          <li>
            <Link to="/dashboard">
              <AiFillAppstore />
              Dashboard
            </Link>
          </li>
          <li>
            <Link to="/profile">
              <AiOutlineUser />
              Profile
            </Link>
          </li>
          <li>
            <Link to="/assignment">
              <AiOutlineLaptop />
              Assignment
            </Link>
          </li>
          <li>
            <Link to="/test">
              <AiOutlineLaptop />
              Electronic Test
            </Link>
          </li>
          <li>
            <Link to="/results">
              <AiFillFund />
              Results
            </Link>
          </li>
          <li>
            <Link to="/#logout">
              <AiOutlineLogout />
              Logout
            </Link>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Navigation;
