import React, { useState, useEffect } from "react";
import AdminNavigation from "../../components/AdminNavigation";
import Header from "../../components/Navigation/Header";
import styles from "../styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import {
  Center,
  CircularProgress,
  useToast,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
  Button,
} from "@chakra-ui/react";
import {
  CREATE_SESSION_RESET,
  DELETE_SESSION_RESET,
} from "../../redux/constants/sessionConstants";
import {
  createNewSession,
  getAllSession,
  deleteSession,
} from "../../redux/actions/sessionActions";
import swal from "sweetalert";

const Session = () => {
  const [name, setName] = useState("");
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [status, setStatus] = useState("");
  const [days, setDays] = useState("");

  const dispatch = useDispatch();
  const toast = useToast();

  useEffect(() => {
    dispatch(getAllSession());
  }, [dispatch]);

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(createNewSession(name, startDate, endDate, status, days));
  };

  const createSession = useSelector((state) => state.createSession);
  const { loading, error, success } = createSession;

  const getSession = useSelector((state) => state.getSession);
  const { session } = getSession;

  const deleteSingleSession = useSelector((state) => state.deleteSingleSession);
  const { success: deleteSuccess } = deleteSingleSession;

  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();

  if (error) {
    toast({
      title: "Error",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: CREATE_SESSION_RESET });
  }

  if (success) {
    toast({
      title: "Notification",
      description: "Class Added successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: CREATE_SESSION_RESET });
    dispatch(getAllSession());
    onClose();
  }
  const me = useSelector((state) => state.me);
  const { user } = me;

  // Delete Session
  const deleteHandler = (id) => {
    swal({
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this class!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        dispatch(deleteSession(id));
        swal("Poof! Your class has been deleted!", {
          icon: "success",
        });
      } else {
        swal("Your class is safe!");
      }
    });
  };

  if (deleteSuccess) {
    toast({
      title: "Notification",
      description: "Class has been deleted",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: DELETE_SESSION_RESET });
    dispatch(getAllSession());
  }

  // Cal Days
  //   var numOfDates = getBusinessDatesCount(startDate,endDate);

  function calcBusinessDays(dDate1, dDate2) {
    // input given as Date objects
    var iWeeks,
      iDateDiff,
      iAdjust = 0;
    if (dDate2 < dDate1) return -1; // error code if dates transposed
    var iWeekday1 = dDate1.getDay(); // day of week
    var iWeekday2 = dDate2.getDay();
    iWeekday1 = iWeekday1 === 0 ? 7 : iWeekday1; // change Sunday from 0 to 7
    iWeekday2 = iWeekday2 === 0 ? 7 : iWeekday2;
    if (iWeekday1 > 5 && iWeekday2 > 5) iAdjust = 1; // adjustment if both days on weekend
    iWeekday1 = iWeekday1 > 5 ? 5 : iWeekday1; // only count weekdays
    iWeekday2 = iWeekday2 > 5 ? 5 : iWeekday2;

    // calculate differnece in weeks (1000mS * 60sec * 60min * 24hrs * 7 days = 604800000)
    iWeeks = Math.floor((dDate2.getTime() - dDate1.getTime()) / 604800000);

    if (iWeekday1 < iWeekday2) {
      //Equal to makes it reduce 5 days
      iDateDiff = iWeeks * 5 + (iWeekday2 - iWeekday1);
    } else {
      iDateDiff = (iWeeks + 1) * 5 - (iWeekday1 - iWeekday2);
    }

    iDateDiff -= iAdjust; // take into account both days on weekend

    return iDateDiff + 1; // add 1 because dates are inclusive
  }

  const CalculateDays = (e) => {
    setEndDate(e.target.value);
    var date1 = new Date(startDate);
    var date2 = new Date(e.target.value);
    const answer = calcBusinessDays(date1, date2);
    setDays(answer);
  };

  return (
    <div>
      <AdminNavigation />
      <div className={styles.page}>
        <Header title="Admin" name={user.firstname} />
        <div className={styles.contentPadding}>
          <Button ref={btnRef} colorScheme="purple" onClick={onOpen}>
            Create Session
          </Button>
          <Drawer
            isOpen={isOpen}
            placement="right"
            onClose={onClose}
            finalFocusRef={btnRef}
          >
            <DrawerOverlay />
            <DrawerContent>
              <DrawerCloseButton />
              <DrawerHeader>Create New Session</DrawerHeader>

              <DrawerBody>
                {loading ? (
                  <Center>
                    {" "}
                    <CircularProgress isIndeterminate color="purple.500" />
                  </Center>
                ) : (
                  <>
                    <div className={styles.inputContainer}>
                      <label>Session Name</label>
                      <input
                        type="text"
                        onChange={(e) => setName(e.target.value)}
                        value={name}
                      />
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Start Date</label>
                      <input
                        type="date"
                        onChange={(e) => setStartDate(e.target.value)}
                        value={startDate}
                      />
                    </div>
                    <div className={styles.inputContainer}>
                      <label>End Date</label>
                      <input
                        type="date"
                        onChange={CalculateDays}
                        value={endDate}
                      />
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Status</label>
                      <select onChange={(e) => setStatus(e.target.value)}>
                        <option>Select ....</option>
                        <option value="InProgress">InProgress</option>
                        <option value="UpComing">UpComing</option>
                      </select>
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Days</label>
                      <input
                        type="text"
                        onChange={(e) => setDays(e.target.value)}
                        value={days}
                        readOnly
                      />
                    </div>
                  </>
                )}
              </DrawerBody>

              <DrawerFooter>
                <Button variant="outline" mr={3} onClick={onClose}>
                  Cancel
                </Button>
                <Button colorScheme="purple" onClick={submitHandler}>
                  Add Session
                </Button>
              </DrawerFooter>
            </DrawerContent>
          </Drawer>

          <div className="tables">
            <Table variant="simple">
              <TableCaption>Session</TableCaption>
              <Thead>
                <Tr>
                  <Th>S/N</Th>
                  <Th>Session</Th>
                  <Th>Status</Th>
                  <Th>Start Date</Th>
                  <Th>End Date</Th>
                  <Th>Days</Th>
                  <Th>Actions</Th>
                </Tr>
              </Thead>
              <Tbody>
                {session.map((item, i) => (
                  <Tr key={i}>
                    <Td>{i + 1}</Td>
                    <Td>{item.name}</Td>
                    <Td>{item.status}</Td>
                    <Td>{item.startDate}</Td>
                    <Td>{item.endDate}</Td>
                    <Td>{item.days}</Td>
                    <Td>
                      <Button type="button" colorScheme="yellow" mr={3}>
                        Edit
                      </Button>
                      <Button
                        type="button"
                        colorScheme="red"
                        onClick={() => deleteHandler(item._id)}
                      >
                        Delete
                      </Button>
                    </Td>
                  </Tr>
                ))}
              </Tbody>
            </Table>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Session;
