import React from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import AdminLogin from "./screens/Admin/AdminLogin";
import AdminDashboard from "./screens/Admin/AdminDashboard";
import Admin from "./screens/Admin/Admin";
import Dashboard from "./screens/Dashboard";
import Login from "./screens/Login";
import Profile from "./screens/Profile";
import Class from "./screens/Admin/Class";
import Subject from "./screens/Admin/Subject";
import Session from "./screens/Admin/Session";
import Instructor from "./screens/Admin/Instructor";

function App() {
  return (
    <>
      <Router>
        <Routes>
          <Route path="/" exact element={<Login />} />
          <Route path="/dashboard" exact element={<Dashboard />} />
          <Route path="/profile" exact element={<Profile />} />

          <Route path="/admin/" exact element={<AdminLogin />} />
          <Route path="/admin/dashboard" exact element={<AdminDashboard />} />
          <Route path="/admin/add" exact element={<Admin />} />
          <Route path="/admin/class" exact element={<Class />} />
          <Route path="/admin/subject" exact element={<Subject />} />
          <Route path="/admin/session" exact element={<Session />} />
          <Route path="/admin/instructor" exact element={<Instructor />} />
        </Routes>
      </Router>
    </>
  );
}

export default App;
