import React, { useState, useEffect } from "react";
import AdminNavigation from "../../components/AdminNavigation";
import Header from "../../components/Navigation/Header";
import styles from "../styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import {
  Center,
  CircularProgress,
  useToast,
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
  Button,
} from "@chakra-ui/react";
import { getAllClass } from "../../redux/actions/classActions";
import swal from "sweetalert";
import {
  createNewSubject,
  getAllSubject,
  deleteSubject,
} from "../../redux/actions/subjectActions";
import {
  CREATE_SUBJECT_RESET,
  DELETE_SUBJECT_RESET,
} from "../../redux/constants/subjectConstants";

const Subject = () => {
  const [name, setName] = useState("");
  const [sclass, setSclass] = useState("");
  const [type, setType] = useState("");

  const dispatch = useDispatch();
  const toast = useToast();

  useEffect(() => {
    dispatch(getAllClass());
    dispatch(getAllSubject());
  }, [dispatch]);

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(createNewSubject(name, sclass, type));
  };

  const createSubject = useSelector((state) => state.createSubject);
  const { loading, error, success } = createSubject;

  const getClass = useSelector((state) => state.getClass);
  const { classes } = getClass;

  const deleteSingleSubject = useSelector((state) => state.deleteSingleSubject);
  const { success: deleteSuccess } = deleteSingleSubject;

  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();

  if (error) {
    toast({
      title: "Error",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: CREATE_SUBJECT_RESET });
  }

  if (success) {
    toast({
      title: "Notification",
      description: "Subject Added successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: CREATE_SUBJECT_RESET });
    dispatch(getAllClass());
    onClose();
  }
  const me = useSelector((state) => state.me);
  const { user } = me;

  // Delete Subject
  const deleteHandler = (id) => {
    swal({
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this subject!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        dispatch(deleteSubject(id));
        swal("Subject has been deleted!", {
          icon: "success",
        });
      } else {
        swal("Subject is safe!");
      }
    });
  };

  if (deleteSuccess) {
    toast({
      title: "Notification",
      description: "Subject has been deleted",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: DELETE_SUBJECT_RESET });
    dispatch(getAllSubject());
  }

  return (
    <div>
      <AdminNavigation />
      <div className={styles.page}>
        <Header title="Admin" name={user.firstname} />
        <div className={styles.contentPadding}>
          <Button ref={btnRef} colorScheme="purple" onClick={onOpen}>
            Add Subject
          </Button>
          <Drawer
            isOpen={isOpen}
            placement="right"
            onClose={onClose}
            finalFocusRef={btnRef}
          >
            <DrawerOverlay />
            <DrawerContent>
              <DrawerCloseButton />
              <DrawerHeader>Add Subjects</DrawerHeader>

              <DrawerBody>
                {loading ? (
                  <Center>
                    {" "}
                    <CircularProgress isIndeterminate color="purple.500" />
                  </Center>
                ) : (
                  <>
                    <div className={styles.inputContainer}>
                      <label>Subject</label>
                      <input
                        type="text"
                        onChange={(e) => setName(e.target.value)}
                        vlaue={name}
                      />
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Class</label>
                      <select onChange={(e) => setSclass(e.target.value)}>
                        <option>Select ....</option>
                        {classes.map((item, i) => (
                          <option value={item._id} key={i}>
                            {item.name}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Subject Type</label>
                      <select onChange={(e) => setType(e.target.value)}>
                        <option>Select ....</option>
                        <option value="Core">Core</option>
                        <option value="Elective">Elective</option>
                      </select>
                    </div>
                  </>
                )}
              </DrawerBody>

              <DrawerFooter>
                <Button variant="outline" mr={3} onClick={onClose}>
                  Cancel
                </Button>
                <Button colorScheme="purple" onClick={submitHandler}>
                  Add Class
                </Button>
              </DrawerFooter>
            </DrawerContent>
          </Drawer>
        </div>
      </div>
    </div>
  );
};

export default Subject;
