import axios from "axios";
import {
  CREATE_CLASS_FAIL,
  CREATE_CLASS_REQUEST,
  CREATE_CLASS_SUCCESS,
  GET_CLASS_FAIL,
  GET_CLASS_REQUEST,
  GET_CLASS_SUCCESS,
  DELETE_CLASS_FAIL,
  DELETE_CLASS_REQUEST,
  DELETE_CLASS_SUCCESS,
  UPDATE_CLASS_FAIL,
  UPDATE_CLASS_REQUEST,
  UPDATE_CLASS_SUCCESS,
  GET_SINGLE_CLASS_FAIL,
  GET_SINGLE_CLASS_REQUEST,
  GET_SINGLE_CLASS_SUCCESS,
} from "../constants/classConstants";

export const createNewClass = (name) => async (dispatch, getState) => {
  try {
    dispatch({ type: CREATE_CLASS_REQUEST });

    const {
      adminLogin: { adminInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
        Authorization: `Bearer ${adminInfo.token}`,
      },
    };

    const { data } = await axios.post(`/api/v1/class`, { name }, config);
    dispatch({
      type: CREATE_CLASS_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: CREATE_CLASS_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};

