import React from "react";
import styles from "./styles.module.css";
import { Link } from "react-router-dom";
import {
  AiFillAppstore,
  AiOutlineUser,
  AiOutlineLaptop,
  AiFillFund,
  AiOutlineLogout,
  AiOutlineUsergroupAdd,
} from "react-icons/ai";
import logo from "../../assets/logo.jpg";

const AdminNavigation = () => {
  return (
    <div className={styles.navigation}>
      <div className={styles.logo}>
        <img src={logo} alt="Logo" />
      </div>
      <div className={styles.url}>
        <ul>
          <li>
            <Link to="/admin/dashboard">
              <AiFillAppstore />
              Dashboard
            </Link>
          </li>
          <li>
            <Link to="/admin/profile">
              <AiOutlineUser />
              Profile
            </Link>
          </li>
          <li>
            <Link to="/admin/add">
              <AiOutlineUsergroupAdd />
              Admin
            </Link>
          </li>
          <li>
            <Link to="/admin/session">
              <AiOutlineUsergroupAdd />
              Academic Session
            </Link>
          </li>
          <li>
            <Link to="/admin/class">
              <AiOutlineUsergroupAdd />
              Class
            </Link>
          </li>
          <li>
            <Link to="/admin/subject">
              <AiOutlineUsergroupAdd />
              Subject
            </Link>
          </li>
          <li>
            <Link to="/admin/instructor">
              <AiOutlineLaptop />
              Instructor
            </Link>
          </li>
          <li>
            <Link to="/test">
              <AiOutlineLaptop />
              Electronic Test
            </Link>
          </li>
          <li>
            <Link to="/results">
              <AiFillFund />
              Results
            </Link>
          </li>
          <li>
            <Link to="/#logout">
              <AiOutlineLogout />
              Logout
            </Link>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default AdminNavigation;
