import axios from "axios";
import {
  ADD_INSTRUCTOR_FAIL,
  ADD_INSTRUCTOR_REQUEST,
  ADD_INSTRUCTOR_SUCCESS,
  GET_INSTRUCTOR_SUCCESS,
  GET_INSTRUCTOR_REQUEST,
  GET_INSTRUCTOR_FAIL,
} from "../constants/instructorConstants";

export const createNewInstructor =
  (
    firstname,
    middlename,
    lastname,
    email,
    mobile,
    gender,
    dob,
    address,
    state,
    lga,
    country,
    password
  ) =>
  async (dispatch, getState) => {
    try {
      dispatch({ type: ADD_INSTRUCTOR_REQUEST });

      const {
        adminLogin: { adminInfo },
      } = getState();

      const config = {
        headers: {
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
          Authorization: `Bearer ${adminInfo.token}`,
        },
      };

      const { data } = await axios.post(
        `/api/v1/instructor`,
        {
          firstname,
          middlename,
          lastname,
          email,
          mobile,
          gender,
          dob,
          address,
          state,
          lga,
          country,
          password,
        },
        config
      );
      dispatch({
        type: ADD_INSTRUCTOR_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: ADD_INSTRUCTOR_FAIL,
        payload:
          error.response && error.response.data.error
            ? error.response.data.error
            : error.message,
      });
    }
  };

export const getAllInstructor = (name) => async (dispatch, getState) => {
  try {
    dispatch({ type: GET_INSTRUCTOR_REQUEST });

    const {
      adminLogin: { adminInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
        Authorization: `Bearer ${adminInfo.token}`,
      },
    };

    const { data } = await axios.get(`/api/v1/instructor`, { name }, config);
    dispatch({
      type: GET_INSTRUCTOR_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: GET_INSTRUCTOR_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};
