import React from "react";
import Cards from "../components/Cards";
import Navigation from "../components/Navigation";
import Header from "../components/Navigation/Header";
import styles from "./styles.module.css";

const Dashboard = () => {
  return (
    <div>
      <Navigation />
      <div className={styles.page}>
        <Header title="Dashboard" name="John Doe" />
        <div className={styles.contentPadding}>
          <div className={styles.cardGrid}>
            <Cards title="Class" count="JSS1" color={styles.red} />
            <Cards title="Subjects" count={10} color={styles.blue} />
            <Cards title="ClassMates" count={50} color={styles.green} />
            <Cards title="Attendance" count="50%" color={styles.purple} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
