import {
  CREATE_CLASS_RESET,
  CREATE_CLASS_FAIL,
  CREATE_CLASS_REQUEST,
  CREATE_CLASS_SUCCESS,
  GET_CLASS_FAIL,
  GET_CLASS_REQUEST,
  GET_CLASS_SUCCESS,
  DELETE_CLASS_FAIL,
  DELETE_CLASS_REQUEST,
  DELETE_CLASS_SUCCESS,
  DELETE_CLASS_RESET,
  UPDATE_CLASS_FAIL,
  UPDATE_CLASS_REQUEST,
  UPDATE_CLASS_SUCCESS,
  UPDATE_CLASS_RESET,
  GET_SINGLE_CLASS_FAIL,
  GET_SINGLE_CLASS_REQUEST,
  GET_SINGLE_CLASS_SUCCESS,
} from "../constants/classConstants";

export const createClassReducer = (state = {}, action) => {
  switch (action.type) {
    case CREATE_CLASS_REQUEST:
      return { loading: true };
    case CREATE_CLASS_SUCCESS:
      return { loading: false, success: true };
    case CREATE_CLASS_FAIL:
      return { loading: false, error: action.payload };
    case CREATE_CLASS_RESET:
      return {};
    default:
      return state;
  }
};

export const getAllClassReducer = (state = { classes: [] }, action) => {
  switch (action.type) {
    case GET_CLASS_REQUEST:
      return { ...state, loading: true };
    case GET_CLASS_SUCCESS:
      return { loading: false, success: true, classes: action.payload.data };
    case GET_CLASS_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

