import { createStore, combineReducers, applyMiddleware } from "redux";
import thunk from "redux-thunk";
import { composeWithDevTools } from "redux-devtools-extension";
import {
  addAdminReducer,
  adminLoginReducer,
  getMeReducer,
} from "./reducers/adminReducers";
import {
  createClassReducer,
  deleteClassReducer,
  getAllClassReducer,
  singleClassReducer,
  updateClassReducer,
} from "./reducers/classReducers";
import {
  createSessionReducer,
  deleteSessionReducer,
  getAllSessionReducer,
  singleSessionReducer,
  updateSessionReducer,
} from "./reducers/sessionReducers";
import {
  classSubjectReducer,
  createSubjectReducer,
  deleteSubjectReducer,
  getAllSubjectReducer,
  singleSubjectReducer,
  updateSubjectReducer,
} from "./reducers/subjectReducers";
import {
  getInstructorReducer,
  instructorReducer,
} from "./reducers/instructorReducers";

const reducer = combineReducers({
  adminLogin: adminLoginReducer,
  addAdmin: addAdminReducer,
  me: getMeReducer,
  createClass: createClassReducer,
  getClass: getAllClassReducer,
  deleteSingleClass: deleteClassReducer,
  updateSingleClass: updateClassReducer,
  singleClass: singleClassReducer,
  createSubject: createSubjectReducer,
  getSubject: getAllSubjectReducer,
  deleteSingleSubject: deleteSubjectReducer,
  updateSingleSubject: updateSubjectReducer,
  singleSubject: singleSubjectReducer,
  classSubject: classSubjectReducer,
  createSession: createSessionReducer,
  getSession: getAllSessionReducer,
  deleteSingleSession: deleteSessionReducer,
  updateSingleSession: updateSessionReducer,
  singleSession: singleSessionReducer,
  createInstructor: instructorReducer,
  getTeacher: getInstructorReducer,
});

const userInfoFromStorage = localStorage.getItem("adminInfo")
  ? JSON.parse(localStorage.getItem("adminInfo"))
  : null;
// const userDetailsFromStorage = localStorage.getItem("user")
//   ? JSON.parse(localStorage.getItem("user"))
//   : null;

const initialState = {
  adminLogin: { adminInfo: userInfoFromStorage },
  // me: { user: userDetailsFromStorage },
};

const middleware = [thunk];

const store = createStore(
  reducer,
  initialState,
  composeWithDevTools(applyMiddleware(...middleware))
);

export default store;
