import {
  CREATE_SESSION_RESET,
  CREATE_SESSION_FAIL,
  CREATE_SESSION_REQUEST,
  CREATE_SESSION_SUCCESS,
  GET_SESSION_FAIL,
  GET_SESSION_REQUEST,
  GET_SESSION_SUCCESS,
  DELETE_SESSION_FAIL,
  DELETE_SESSION_REQUEST,
  DELETE_SESSION_SUCCESS,
  DELETE_SESSION_RESET,
  UPDATE_SESSION_FAIL,
  UPDATE_SESSION_REQUEST,
  UPDATE_SESSION_SUCCESS,
  UPDATE_SESSION_RESET,
  GET_SINGLE_SESSION_FAIL,
  GET_SINGLE_SESSION_REQUEST,
  GET_SINGLE_SESSION_SUCCESS,
} from "../constants/sessionConstants";

export const createSessionReducer = (state = {}, action) => {
  switch (action.type) {
    case CREATE_SESSION_REQUEST:
      return { loading: true };
    case CREATE_SESSION_SUCCESS:
      return { loading: false, success: true };
    case CREATE_SESSION_FAIL:
      return { loading: false, error: action.payload };
    case CREATE_SESSION_RESET:
      return {};
    default:
      return state;
  }
};

export const getAllSessionReducer = (state = { session: [] }, action) => {
  switch (action.type) {
    case GET_SESSION_REQUEST:
      return { ...state, loading: true };
    case GET_SESSION_SUCCESS:
      return { loading: false, success: true, session: action.payload.data };
    case GET_SESSION_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

