import React, { useState, useEffect } from "react";
import AdminNavigation from "../../components/AdminNavigation";
import Header from "../../components/Navigation/Header";
import styles from "../styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import {
  Center,
  CircularProgress,
  useToast,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
  Button,
  Input,
  Select,
  Textarea,
} from "@chakra-ui/react";
import { DELETE_CLASS_RESET } from "../../redux/constants/classConstants";

import {
  createNewInstructor,
  getAllInstructor,
} from "../../redux/actions/instructorActions";
import swal from "sweetalert";
import { ADD_INSTRUCTOR_RESET } from "../../redux/constants/instructorConstants";

const Instructor = () => {
  const [firstname, setFirstname] = useState("");
  const [middlename, setMiddlename] = useState("");
  const [lastname, setLastname] = useState("");
  const [email, setEmail] = useState("");
  const [mobile, setMobile] = useState("");
  const [gender, setGender] = useState("");
  const [dob, setDob] = useState("");
  const [address, setAddress] = useState("");
  const [state, setState] = useState("");
  const [lga, setLga] = useState("");
  const [country, setCountry] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  const dispatch = useDispatch();
  const toast = useToast();

  useEffect(() => {
    dispatch(getAllInstructor());
  }, [dispatch]);

  const submitHandler = (e) => {
    e.preventDefault();
    if (password === confirmPassword) {
      dispatch(
        createNewInstructor(
          firstname,
          middlename,
          lastname,
          email,
          mobile,
          gender,
          dob,
          address,
          state,
          lga,
          country,
          password
        )
      );
    } else {
      toast({
        title: "Error",
        description: "Password does not match",
        status: "error",
        duration: 9000,
        isClosable: true,
      });
    }
  };

  const createInstructor = useSelector((state) => state.createInstructor);
  const { loading, error, success } = createInstructor;

  const getClass = useSelector((state) => state.getClass);
  const { classes } = getClass;

  const deleteSingleClass = useSelector((state) => state.deleteSingleClass);
  const { success: deleteSuccess } = deleteSingleClass;

  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();

  if (error) {
    toast({
      title: "Error",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: ADD_INSTRUCTOR_RESET });
  }

  if (success) {
    toast({
      title: "Notification",
      description: "Instructor Added successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: ADD_INSTRUCTOR_RESET });
    dispatch(getAllInstructor());
    onClose();
  }
  const me = useSelector((state) => state.me);
  const { user } = me;

  // Delete Class
  const deleteHandler = (id) => {
    swal({
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this class!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        // dispatch(deleteClass(id));
        swal("Poof! Your class has been deleted!", {
          icon: "success",
        });
      } else {
        swal("Your class is safe!");
      }
    });
  };

  if (deleteSuccess) {
    toast({
      title: "Notification",
      description: "Class has been deleted",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: DELETE_CLASS_RESET });
    dispatch(getAllInstructor());
  }

  return (
    <div>
      <AdminNavigation />
      <div className={styles.page}>
        <Header title="Admin" name={user.firstname} />
        <div className={styles.contentPadding}>
          <Button ref={btnRef} colorScheme="purple" onClick={onOpen}>
            Add Instructor
          </Button>
          <Drawer
            isOpen={isOpen}
            placement="right"
            onClose={onClose}
            finalFocusRef={btnRef}
            size="lg"
          >
            <DrawerOverlay />
            <DrawerContent>
              <DrawerCloseButton />
              <DrawerHeader>Add Instructor</DrawerHeader>

              <DrawerBody>
                {loading ? (
                  <Center>
                    {" "}
                    <CircularProgress isIndeterminate color="purple.500" />
                  </Center>
                ) : (
                  <div className={styles.rows}>
                    <div className={styles.inputContainer}>
                      <label>Firstname</label>
                      <Input
                        type="text"
                        onChange={(e) => setFirstname(e.target.value)}
                        value={firstname}
                        required
                      />
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Middlename</label>
                      <Input
                        type="text"
                        onChange={(e) => setMiddlename(e.target.value)}
                        value={middlename}
                        required
                      />
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Lastname</label>
                      <Input
                        type="text"
                        onChange={(e) => setLastname(e.target.value)}
                        value={lastname}
                        required
                      />
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Email</label>
                      <Input
                        type="email"
                        onChange={(e) => setEmail(e.target.value)}
                        value={email}
                        required
                      />
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Mobile</label>
                      <Input
                        type="tel"
                        onChange={(e) => setMobile(e.target.value)}
                        value={mobile}
                        required
                      />
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Mobile</label>
                      <Input
                        type="date"
                        onChange={(e) => setDob(e.target.value)}
                        value={dob}
                        required
                      />
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Gender</label>
                      <Select onChange={(e) => setGender(e.target.value)}>
                        <option>Select ....</option>
                        <option value="Male">Male</option>
                        <option value="Female">Female</option>
                      </Select>
                    </div>

                    <div className={styles.inputContainer}>
                      <label>Country</label>
                      <Select onChange={(e) => setCountry(e.target.value)}>
                        <option>Select ....</option>
                        <option value="Nigeria">Nigeria</option>
                      </Select>
                    </div>
                    <div className={styles.inputContainer}>
                      <label>State</label>
                      <Select onChange={(e) => setState(e.target.value)}>
                        <option>Select ....</option>
                      </Select>
                    </div>
                    <div className={styles.inputContainer}>
                      <label>LGA</label>
                      <Input
                        type="text"
                        onChange={(e) => setLga(e.target.value)}
                        value={lga}
                        required
                      />
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Password</label>
                      <Input
                        type="password"
                        onChange={(e) => setPassword(e.target.value)}
                        value={password}
                        required
                      />
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Password</label>
                      <Input
                        type="password"
                        onChange={(e) => setConfirmPassword(e.target.value)}
                        value={confirmPassword}
                        required
                      />
                    </div>
                    <div className={styles.inputContainer}>
                      <label>Address</label>
                      <Textarea
                        onChange={(e) => setAddress(e.target.value)}
                      ></Textarea>
                    </div>
                  </div>
                )}
              </DrawerBody>

              <DrawerFooter>
                <Button variant="outline" mr={3} onClick={onClose}>
                  Cancel
                </Button>
                <Button colorScheme="purple" onClick={submitHandler}>
                  Add Instructor
                </Button>
              </DrawerFooter>
            </DrawerContent>
          </Drawer>

          <div className="tables">
            <Table variant="simple">
              <TableCaption>Classes</TableCaption>
              <Thead>
                <Tr>
                  <Th>S/N</Th>
                  <Th>Class</Th>
                  <Th>Actions</Th>
                </Tr>
              </Thead>
              <Tbody>
                {classes.map((item, i) => (
                  <Tr key={i}>
                    <Td>{i + 1}</Td>
                    <Td>{item.name}</Td>
                    <Td>
                      <Button type="button" colorScheme="yellow" mr={3}>
                        Edit
                      </Button>
                      <Button
                        type="button"
                        colorScheme="red"
                        onClick={() => deleteHandler(item._id)}
                      >
                        Delete
                      </Button>
                    </Td>
                  </Tr>
                ))}
              </Tbody>
            </Table>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Instructor;
