import axios from "axios";
import {
  CREATE_SESSION_FAIL,
  CREATE_SESSION_REQUEST,
  CREATE_SESSION_SUCCESS,
  GET_SESSION_FAIL,
  GET_SESSION_REQUEST,
  GET_SESSION_SUCCESS,
  DELETE_SESSION_FAIL,
  DELETE_SESSION_REQUEST,
  DELETE_SESSION_SUCCESS,
  UPDATE_SESSION_FAIL,
  UPDATE_SESSION_REQUEST,
  UPDATE_SESSION_SUCCESS,
  GET_SINGLE_SESSION_FAIL,
  GET_SINGLE_SESSION_REQUEST,
  GET_SINGLE_SESSION_SUCCESS,
} from "../constants/sessionConstants";

export const createNewSession =
  (name, startDate, endDate, status, days) => async (dispatch, getState) => {
    try {
      dispatch({ type: CREATE_SESSION_REQUEST });

      const {
        adminLogin: { adminInfo },
      } = getState();

      const config = {
        headers: {
          "Content-Type": "application/json",
          "Access-Control-Allow-Origin": "*",
          Authorization: `Bearer ${adminInfo.token}`,
        },
      };

      const { data } = await axios.post(
        `/api/v1/session`,
        { name, startDate, endDate, status, days },
        config
      );
      dispatch({
        type: CREATE_SESSION_SUCCESS,
        payload: data,
      });
    } catch (error) {
      dispatch({
        type: CREATE_SESSION_FAIL,
        payload:
          error.response && error.response.data.error
            ? error.response.data.error
            : error.message,
      });
    }
  };

export const getAllSession = () => async (dispatch, getState) => {
  try {
    dispatch({ type: GET_SESSION_REQUEST });

    const {
      adminLogin: { adminInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        "Access-Control-Allow-Origin": "*",
        Authorization: `Bearer ${adminInfo.token}`,
      },
    };

    const { data } = await axios.get(`/api/v1/session`, config);
    dispatch({
      type: GET_SESSION_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: GET_SESSION_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};

