import React, { useEffect } from "react";
import Cards from "../../components/Cards";
import AdminNavigation from "../../components/AdminNavigation";
import Header from "../../components/Navigation/Header";
import styles from "../styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import { getMe } from "../../redux/actions/adminActions";

const AdminDashboard = () => {
  const dispatch = useDispatch();

  const me = useSelector((state) => state.me);
  const { user } = me;

  useEffect(() => {
    dispatch(getMe());
  }, [dispatch]);
  return (
    <div>
      <AdminNavigation />
      <div className={styles.page}>
        <Header title="Admin" name={user.firstname} />
        <div className={styles.contentPadding}>
          <div className={styles.cardGrid}>
            <Cards title="Class" count="JSS1" color={styles.red} />
            <Cards title="Subjects" count={10} color={styles.blue} />
            <Cards title="ClassMates" count={50} color={styles.green} />
            <Cards title="Attendance" count="50%" color={styles.purple} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminDashboard;
