import React, { useState, useEffect } from "react";
import styles from "../login.module.css";
import { AiOutlineLogin } from "react-icons/ai";
import { Link, useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { auth } from "../../redux/actions/adminActions";
import { Center, CircularProgress, useToast } from "@chakra-ui/react";
import { ADMIN_LOGOUT } from "../../redux/constants/adminConstants";

const AdminLogin = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const toast = useToast();

  const loginHandler = (e) => {
    e.preventDefault();
    dispatch(auth(email, password));
  };

  const adminLogin = useSelector((state) => state.adminLogin);
  const { loading, error, adminInfo } = adminLogin;

  useEffect(() => {
    if (adminInfo) {
      navigate("/admin/dashboard");
    }
  }, [navigate, adminInfo]);

  if (error) {
    toast({
      title: "Error",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: ADMIN_LOGOUT });
  }

  return (
    <div className={styles.login}>
      <div className={styles.loginGrid}>
        <div className={`${styles.loginCard} ${styles.bg}`}></div>
        <div className={`${styles.loginCard} ${styles.loginPadding}`}>
          <h3>Hello Again!</h3>
          <h5>
            Welcome Back to <span>iProfit School</span>
          </h5>
          <div className={styles.formTopPadding}>
            {loading ? (
              <Center>
                <CircularProgress isIndeterminate color="purple.500" />
              </Center>
            ) : (
              <form onSubmit={loginHandler}>
                <div className={styles.inputContainer}>
                  <input
                    type="email"
                    placeholder="Email Address"
                    onChange={(e) => setEmail(e.target.value)}
                    value={email}
                  />
                </div>
                <div className={styles.inputContainer}>
                  <input
                    type="password"
                    placeholder="Password"
                    onChange={(e) => setPassword(e.target.value)}
                    value={password}
                  />
                </div>
                <div className={styles.inputContainer}>
                  <Link to="/forgotPassword" className={styles.right}>
                    Forgot Password?
                  </Link>
                </div>
                <div className={styles.inputContainer}>
                  <button type="submit" className={styles.purple}>
                    Login <AiOutlineLogin />
                  </button>
                </div>
              </form>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminLogin;
