import {
  ADD_INSTRUCTOR_FAIL,
  ADD_INSTRUCTOR_REQUEST,
  ADD_INSTRUCTOR_RESET,
  ADD_INSTRUCTOR_SUCCESS,
  GET_INSTRUCTOR_FAIL,
  GET_INSTRUCTOR_REQUEST,
  GET_INSTRUCTOR_SUCCESS,
} from "../constants/instructorConstants";

export const instructorReducer = (state = {}, action) => {
  switch (action.type) {
    case ADD_INSTRUCTOR_REQUEST:
      return { loading: true };
    case ADD_INSTRUCTOR_SUCCESS:
      return { loading: false, success: true };
    case ADD_INSTRUCTOR_FAIL:
      return { loading: false, error: action.payload };
    case ADD_INSTRUCTOR_RESET:
      return {};
    default:
      return state;
  }
};

export const getInstructorReducer = (state = { teacher: [] }, action) => {
  switch (action.type) {
    case GET_INSTRUCTOR_REQUEST:
      return { ...state, loading: true };
    case GET_INSTRUCTOR_SUCCESS:
      return { loading: false, success: true, teacher: action.payload.data };
    case GET_INSTRUCTOR_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
