import React, { useState } from "react";
import styles from "./login.module.css";
import { AiOutlineLogin } from "react-icons/ai";
import { Link } from "react-router-dom";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  return (
    <div className={styles.login}>
      <div className={styles.loginGrid}>
        <div className={`${styles.loginCard} ${styles.bg}`}></div>
        <div className={`${styles.loginCard} ${styles.loginPadding}`}>
          <h3>Hello Again!</h3>
          <h5>
            Welcome Back to <span>iProfit School</span>
          </h5>
          <div className={styles.formTopPadding}>
            <form>
              <div className={styles.inputContainer}>
                <input
                  type="email"
                  placeholder="Email Address"
                  onChange={(e) => setEmail(e.target.value)}
                  value={email}
                />
              </div>
              <div className={styles.inputContainer}>
                <input
                  type="password"
                  placeholder="Password"
                  onChange={(e) => setPassword(e.target.value)}
                  value={password}
                />
              </div>
              <div className={styles.inputContainer}>
                <Link to="/forgotPassword" className={styles.right}>
                  Forgot Password?
                </Link>
              </div>
              <div className={styles.inputContainer}>
                <button type="submit" className={styles.purple}>
                  Login <AiOutlineLogin />
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
