import React, { useState, useEffect } from "react";
import AdminNavigation from "../../components/AdminNavigation";
import Header from "../../components/Navigation/Header";
import styles from "../styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import {
  Center,
  CircularProgress,
  useToast,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
  Button,
} from "@chakra-ui/react";
import {
  CREATE_CLASS_RESET,
  DELETE_CLASS_RESET,
} from "../../redux/constants/classConstants";
import {
  createNewClass,
  getAllClass,
  deleteClass,
} from "../../redux/actions/classActions";
import swal from "sweetalert";

const Class = () => {
  const [name, setName] = useState("");

  const dispatch = useDispatch();
  const toast = useToast();

  useEffect(() => {
    dispatch(getAllClass());
  }, [dispatch]);

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(createNewClass(name));
  };

  const createClass = useSelector((state) => state.createClass);
  const { loading, error, success } = createClass;

  const getClass = useSelector((state) => state.getClass);
  const { classes } = getClass;

  const deleteSingleClass = useSelector((state) => state.deleteSingleClass);
  const { success: deleteSuccess } = deleteSingleClass;

  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();

  if (error) {
    toast({
      title: "Error",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: CREATE_CLASS_RESET });
  }

  if (success) {
    toast({
      title: "Notification",
      description: "Class Added successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: CREATE_CLASS_RESET });
    dispatch(getAllClass());
    onClose();
  }
  const me = useSelector((state) => state.me);
  const { user } = me;

  // Delete Class
  const deleteHandler = (id) => {
    swal({
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this class!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        dispatch(deleteClass(id));
        swal("Poof! Your class has been deleted!", {
          icon: "success",
        });
      } else {
        swal("Your class is safe!");
      }
    });
  };

  if (deleteSuccess) {
    toast({
      title: "Notification",
      description: "Class has been deleted",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: DELETE_CLASS_RESET });
    dispatch(getAllClass());
  }

  return (
    <div>
      <AdminNavigation />
      <div className={styles.page}>
        <Header title="Admin" name={user.firstname} />
        <div className={styles.contentPadding}>
          <Button ref={btnRef} colorScheme="purple" onClick={onOpen}>
            Create Class
          </Button>
          <Drawer
            isOpen={isOpen}
            placement="right"
            onClose={onClose}
            finalFocusRef={btnRef}
          >
            <DrawerOverlay />
            <DrawerContent>
              <DrawerCloseButton />
              <DrawerHeader>Create New Class</DrawerHeader>

              <DrawerBody>
                {loading ? (
                  <Center>
                    {" "}
                    <CircularProgress isIndeterminate color="purple.500" />
                  </Center>
                ) : (
                  <div className={styles.inputContainer}>
                    <label>Class Name</label>
                    <input
                      type="text"
                      onChange={(e) => setName(e.target.value)}
                      value={name}
                    />
                  </div>
                )}
              </DrawerBody>

              <DrawerFooter>
                <Button variant="outline" mr={3} onClick={onClose}>
                  Cancel
                </Button>
                <Button colorScheme="purple" onClick={submitHandler}>
                  Add Class
                </Button>
              </DrawerFooter>
            </DrawerContent>
          </Drawer>

          <div className="tables">
            <Table variant="simple">
              <TableCaption>Classes</TableCaption>
              <Thead>
                <Tr>
                  <Th>S/N</Th>
                  <Th>Class</Th>
                  <Th>Actions</Th>
                </Tr>
              </Thead>
              <Tbody>
                {classes.map((item, i) => (
                  <Tr key={i}>
                    <Td>{i + 1}</Td>
                    <Td>{item.name}</Td>
                    <Td>
                      <Button type="button" colorScheme="yellow" mr={3}>
                        Edit
                      </Button>
                      <Button
                        type="button"
                        colorScheme="red"
                        onClick={() => deleteHandler(item._id)}
                      >
                        Delete
                      </Button>
                    </Td>
                  </Tr>
                ))}
              </Tbody>
            </Table>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Class;
