import {
  ADMIN_LOGIN_REQUEST,
  ADMIN_LOGIN_SUCCESS,
  ADMIN_LOGIN_FAIL,
  ADMIN_LOGOUT,
  ADMIN_ADD_FAIL,
  ADMIN_ADD_REQUEST,
  ADMIN_ADD_RESET,
  ADMIN_ADD_SUCCESS,
  GET_ME_REQUEST,
  GET_ME_SUCCESS,
  GET_ME_FAIL,
} from "../constants/adminConstants";

export const adminLoginReducer = (state = {}, action) => {
  switch (action.type) {
    case ADMIN_LOGIN_REQUEST:
      return { loading: true };
    case ADMIN_LOGIN_SUCCESS:
      return { loading: false, adminInfo: action.payload };
    case ADMIN_LOGIN_FAIL:
      return { loading: false, error: action.payload };
    case ADMIN_LOGOUT:
      return {};
    default:
      return state;
  }
};

export const addAdminReducer = (state = {}, action) => {
  switch (action.type) {
    case ADMIN_ADD_REQUEST:
      return { loading: true };
    case ADMIN_ADD_SUCCESS:
      return { loading: false, success: true };
    case ADMIN_ADD_FAIL:
      return { loading: false, error: action.payload };
    case ADMIN_ADD_RESET:
      return {};
    default:
      return state;
  }
};

export const getMeReducer = (state = { user: { firstname: "" } }, action) => {
  switch (action.type) {
    case GET_ME_REQUEST:
      return { ...state, loading: true };
    case GET_ME_SUCCESS:
      return { loading: false, success: true, user: action.payload.data };
    case GET_ME_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
