import {
  CREATE_SUBJECT_RESET,
  CREATE_SUBJECT_FAIL,
  CREATE_SUBJECT_REQUEST,
  CREATE_SUBJECT_SUCCESS,
  GET_SUBJECT_FAIL,
  GET_SUBJECT_REQUEST,
  GET_SUBJECT_SUCCESS,
  DELETE_SUBJECT_FAIL,
  DELETE_SUBJECT_REQUEST,
  DELETE_SUBJECT_SUCCESS,
  DELETE_SUBJECT_RESET,
  UPDATE_SUBJECT_FAIL,
  UPDATE_SUBJECT_REQUEST,
  UPDATE_SUBJECT_SUCCESS,
  UPDATE_SUBJECT_RESET,
  GET_SINGLE_SUBJECT_FAIL,
  GET_SINGLE_SUBJECT_REQUEST,
  GET_SINGLE_SUBJECT_SUCCESS,
  GET_CLASS_SUBJECT_FAIL,
  GET_CLASS_SUBJECT_REQUEST,
  GET_CLASS_SUBJECT_RESET,
  GET_CLASS_SUBJECT_SUCCESS,
} from "../constants/subjectConstants";

export const createSubjectReducer = (state = {}, action) => {
  switch (action.type) {
    case CREATE_SUBJECT_REQUEST:
      return { loading: true };
    case CREATE_SUBJECT_SUCCESS:
      return { loading: false, success: true };
    case CREATE_SUBJECT_FAIL:
      return { loading: false, error: action.payload };
    case CREATE_SUBJECT_RESET:
      return {};
    default:
      return state;
  }
};

export const getAllSubjectReducer = (state = { subjects: [] }, action) => {
  switch (action.type) {
    case GET_SUBJECT_REQUEST:
      return { ...state, loading: true };
    case GET_SUBJECT_SUCCESS:
      return { loading: false, success: true, subjects: action.payload.data };
    case GET_SUBJECT_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

